import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index'
import Manage from './views/manage/index'
import MyOrder from './views/MyOrder'
import Login from './components/Login'
import Books from './views/books'
import Details from './views/Details'
import Cart from './views/cart'
import Register from './views/register'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/manage',
      name: 'Manage',
      component: Manage
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/books',
      name: 'Books',
      component: Books
    },
    {
      path: '/Details',
      name: 'Details',
      component: Details
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/MyOrder',
      name: 'MyOrder',
      component: MyOrder
    }
  ]
})
