<template>
  <div>
      <el-row>
          <uper></uper>
      </el-row>
      <el-container>
        <el-aside width="50%"></el-aside>
        <el-main >
            <el-row>
                <el-col span=4><p class="Description">user name:</p></el-col>
                <el-col span=12>
                    <el-input placeholder="Please input your name." v-model="username" clearable></el-input>
                </el-col>
            </el-row>
            <br>
            <el-row>
                    <el-col span=4><p class="Description">password:</p></el-col>
                    <el-col span=12>
                    <el-input placeholder="Please input your password." v-model="password" show-password></el-input>
                    </el-col>
            </el-row>
            <br>
            <el-row>
                    <el-col span=4><p class="Description">Confirm your password:</p></el-col>
                    <el-col span=12>
                    <el-input placeholder="Confirm your password" v-model="check" show-password></el-input>
                    </el-col>
            </el-row>
            <br>
            <el-row>
                    <el-col span=4><p class="Description">e-mail</p></el-col>
                    <el-col span=12>
                    <el-input placeholder="Please input your e-mail" v-model="email" clearable></el-input>
                    </el-col>
            </el-row>
            <br>
            <el-row>
                    <el-col span=18><el-button type="primary" @click="upload">submit</el-button></el-col>
            </el-row>
        </el-main>
      </el-container>
  </div>
</template>

<script>
import uper from '../components/up-column'


export default {
  data () {
    return {
       username:'',
       password:'',
       check:'',
       rights:'',
       email:'',
    }
  },
  name: 'cart',
  components: {uper},
  methods: {
      upload(){
          if(this.password == this.check)
          {
              if(this.checkEmail())
              {
               this.$axios.post('http://localhost:8080/register',{
                   username: this.username,
                   password: this.password,
                   rights: this.rights,
                   email: this.email
                 }).then((response) => {
                     if(response.data.username=='exist')

                         alert('User name exists.');

                     else if(response.data.rights){
                         console.log(response.data);
                         console.log("Access:"+response.data.rights);
                         console.log("UserName:"+response.data.username);
                         alert('Succeed!');
                         this.global.setUserName(this.username);
                         this.global.setAccess(response.data.rights);
                         this.global.setStatus(1);
                         this.$router.push({ path: '/', query: { username: this.username }});
                     }
                     else {
                         console.log(response.data);
                         alert('Error!');
                     }

                 }).catch((response) => {
                    alert(response);
                 });
              }
              else
              {
               alert('Something wrong with your e-mail address.')
              }
          }
          else
          {
              alert('Fail to confirm your password.');
          }
      },
      checkEmail(){
         var myReg=/^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/;
         if(myReg.test(this.email))
        {
         return true;
         }
        else
        {
         return false;
        }
     }
   }
}
</script>

