<template>
  <div>
    <uper></uper>
    <footer-nav></footer-nav>
    <div class="block">
    <span class="demonstration">data range</span>
    <el-date-picker
    value-format="yyyy-MM-dd"
    unlink-panels=true
      type="daterange"
      range-separator="to"
      v-model="dateSelect"
      start-placeholder="start date"
      end-placeholder="end date">
    </el-date-picker>
  </div>

    <div>
    <h1 class="Title">My Order</h1>
    <input type="button" v-on:click="review()" value='click to load my order'/>
    </div>
    <el-table :data="OrderData.filter(data=>!dateSelect || data.time>=dateSelect[0] && data.time <= dateSelect[1])"
              stripe
              style="width: 100% ;">
      <el-table-column>
        
        <el-table-column
          prop="bookID"
          label="book_id"
          sortable
        ></el-table-column>
        <el-table-column
          prop="bookname"
          label="bookname"
          sortable
        ></el-table-column>
        <el-table-column
          prop="userName"
          label="userName"
          sortable
        ></el-table-column>
        <el-table-column
          prop="count"
          label="count"
          sortable
        ></el-table-column>
        <el-table-column
          prop="orderID"
          label="order_id"
          sortable
        ></el-table-column>
         <el-table-column
          prop="price"
          label="price"
          sortable
        ></el-table-column>
        <el-table-column
          prop="time"
          label="time"
          sortable
        ></el-table-column>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import uper from '../components/up-column'
import FooterNav from '../components/footer'

export default {
  data () {
    return {
      search: '',
      OrderData: [],
      dateSelect: []

    }
  },
  methods:{
    

    review() {
        if (this.dateSelect == '') {
            this.$message({
            message: 'Warning，please select time!',
            type: 'warning'
        });
        return;
        }

        this.$axios.post('http://localhost:8080/getOrder',{
          username:this.global.username
        }).then((response) => {
          console.log(response.data);
          this.OrderData=response.data;
        }).catch((response) => {
          alert(response);
        });
    },
  },
  name: 'MyOrder',
  components: {uper, FooterNav}
}
</script>

<style scoped>
@import "../css/books.css";
</style>
