<template>
  <div>
    <uper></uper>
    <footer-nav></footer-nav>
    <div>
    <h1 class="Title">My cart</h1>
    <input type="button" v-on:click="review" value='click to enter the cart'/>
    </div>
    <el-table :data="cartData"
              stripe
              style="width: 100%"
              max-height="">
      <el-table-column>
        <el-table-column
          type="index">
        </el-table-column>
        <el-table-column
          prop="bookName"
          label="bookName"
          sortable
        ></el-table-column>
         <el-table-column
          prop="price"
          label="price"
          sortable
        ></el-table-column>
        <el-table-column
          prop="number"
          label="number"
          sortable
        ></el-table-column>
        
        <el-table-column
      fixed="right"
      label="Delete"
      width="120">
      <template slot-scope="scope">
        <el-button
          @click="deleteRow(scope.$index, cartData)"
          type="text"
          size="small">
          delete
        </el-button>
      </template>
    </el-table-column>


      </el-table-column>
    </el-table>
    <p>
  <button  type="primary" @click="submitCart">buy</button>
</p>
  </div>
</template>
<script>
import uper from '../components/up-column'
import FooterNav from '../components/footer'

export default {
  data () {
    return {
      search:'',
      cartData: []
    }
  },
  methods:{
    review() {
        this.$axios.post('http://localhost:8080/cart/cartlist',{username:this.global.username}).then((response) => {
          console.log(this.global.username);
          console.log(response.data);
          this.cartData=response.data;
        }).catch((response) => {
           alert(response);
        });
    },
    deleteRow(index, rows){
           this.$axios.post('http://localhost:8080/cart/deleteCart',{
                    bookName:rows[index].bookName,
                    username:this.global.username,
                    number:rows[index].number
                  }).then((response) => {
                     alert(rows[index].bookName+" is removed!");
                     this.review();
                 }).catch((response) => {
                    alert(response);
                 });
         },

         submitCart(){
           this.$axios.post('http://localhost:8080/cart/submitCart',{
                    username:this.global.username,
                  }).then((response) => {
                     alert("Sucess!");
                     this.review();
                 }).catch((response) => {
                    alert(response);
                 });
         },
  },
  name: 'cart',
  components: {uper, FooterNav}
}
</script>

<style scoped>
@import "../css/books.css";
</style>
