<template>
  <div id="uper" >
    <el-row>
      <el-col span="5">
        <p class="Description">{{this.message}}</p>
      </el-col>
      <el-col span="18">
        
         <nav v-bind:class="active" v-on:click.prevent>
      <router-link to='/' class="home">index</router-link>
      <router-link to='/books' class="projects">booklist</router-link>
      <a href=""  class="contact" v-on:click="Logout">log out</a>
      <a href=""  class="contact" v-on:click="showLogin">log in</a>
      <router-link to='/register' class="projects">register</router-link>
    </nav>
      </el-col>
      
    </el-row>
    <div class="login" id="login">
      <form action="" method="post">
        <div class="item firstLine" id="firstLine">
          <span class="login_title">log in</span>
          <span class="login_close" id="close_minilogin" v-on:click="closeLogin">X</span>
        </div>
        <div class="item">
          <label>user name</label>
          <input type="text" v-model="uname" />
        </div>
        <div class="item">
          <label>password</label>
          <input type="password" v-model="upwd" />
        </div>
        <div class="item">
           <el-button id="lolo" type="primary" :loading="false" v-on:click="decide">log in</el-button>
        </div>
      </form>
    </div>
    
  </div>
</template>

<script>
  export default {
    data () {
      return{
      uname:'',
      upwd:'',
      haveLogin:0,
      message:'',
      }
    },
    name: 'uperNav',
    mounted(){
     this.welcome();

    },
    methods:{
    showLogin: function () {
      if(this.global.getStatus() == 0){
        var login = document.getElementById('login')
        login.style.display = 'block'
        login.style.left = (document.body.scrollWidth - login.scrollWidth) / 2 + 'px'
        login.style.top = (document.body.scrollHeight - login.scrollHeight) / 2 + 2*'px'
      }
      else
        alert('You have logged in!');
    },
    closeLogin: function () {

      var login = document.getElementById('login')
      login.style.display = 'none'
      this.uname='';
      this.upwd='';
      this.welcome();
    },
    moveLogin: function (event) {
      var moveable = true

      event = event ? event : window.event
      var clientX = event.clientX
      var clientY = event.clientY

      var login = document.getElementById('login')
      console.log(login)
      var top = parseInt(login.style.top)
      var left = parseInt(login.style.left)
      document.onmousemove = function (event) {
        if (moveable) {
          event = event ? event : window.event
          var y = top + event.clientY - clientY
          var x = left + event.clientX - clientX
          if (x > 0 && y > 0) {
            login.style.top = y + 'px'
            login.style.left = x + 'px'
          }
        }
      }
      document.onmouseup = function () {
        moveable = false
      }
    },
    decide(){
        this.$axios.post('http://localhost:8080/login',{
          username: this.uname,
          password: this.upwd
        }).then((response) => {
          console.log(response.data);
          if(response.data.access == 0)
          alert('You have been banned from logging in.');
          else if (response.data.password == this.upwd){
          this.global.setUserName(this.uname);
          this.global.setAccess(response.data.access);
          this.global.setStatus(1);
          this.welcome();
          alert('Success!');
          this.closeLogin();
          }
          else{
           alert('Wrong password!');
          }
        }).catch((response) => {
          alert(response);
        });
    },
    welcome(){
      if(this.global.getStatus()){
         this.message = 'Welcome,'+this.global.username+'!';
      }
      else
      this.message = 'Login in to enter the e-book';
    },
    Logout:function () {
      this.global.Exit();
      this.welcome();
    }
    }
  }
</script>

<style src="../css/up-column.css">

</style>
