export default {
  username: '',
  8080: '',
  status: 0,
  setUserName (user) {
    this.username = user
  },
  setAccess (r) {
    this.access = r
  },
  setStatus (a) {
    this.status = a
  },
  getUserName () {
    return this.username
  },
  getStatus () {
    return this.status
  },
  Exit () {
    this.username = ''
    this.access = true
    this.status = 0
  }
}
