<template>
  <div>
      <h1 class="Title">e-book shop</h1>
      <uper></uper>
      <footer-nav></footer-nav>
       <el-button type="info"  @click.native.prevent="review">click to view the booklist</el-button>
    <el-input v-model="search"
              border
              style="width: 100% ; margin-bottom: 25px "
              placeholder="Please input book name"
              prefix-icon="el-icon-search"/>
    <el-table :data="tableData.filter(data=>!search || data.bookName.includes(search))"
              @row-click = 'details'
              style="width: 100% ;">
      <el-table-column>
        <el-table-column
          type="index">
        </el-table-column>
        
        <template slot-scope="scope">
        <img  :src="scope.row.href" alt="" style="width: 120px;height: 150px">
        </template>
        </el-table-column>
        <el-table-column
          prop="bookName"
          label="bookName"
          sortable
        ></el-table-column>
        <el-table-column
          prop="author"
          label="author"
          sortable
        ></el-table-column>
        <el-table-column
          prop="iSBN"
          label="ISBN"
          sortable
        ></el-table-column>
        <el-table-column
          prop="price"
          label="price"
          sortable
        ></el-table-column>
        <el-table-column
          prop="stock"
          label="stock"
          sortable
        ></el-table-column>

    </el-table>
  </div>
</template>

<script>

import uper from '../components/up-column'
import FooterNav from '../components/footer.vue'

export default {
  name: 'Table',
  components: {uper, FooterNav},
  data () {
    return {
      search: '',
      color:'black',
      tableData: []
    }
  },
  methods:{
    details(row) {
        this.$router.push({ path: '/Details', query: { bookName: row.bookName }});
      },
    review() {
        this.$axios.get('http://localhost:8080/book/table').then((response) => {
          console.log(response.data);
          this.tableData=response.data;
          console.log(this.tableData);
        }).catch((response) => {
          alert(response);
        });
    }
  }
}
</script>

<style scoped>
@import "../css/books.css";
