          });
      },*/
    methods: {
         cartbook(){
           this.$axios.post('http://localhost:8080/cart/cartbook',{
                    username:this.global.username,
                    bookName:this.bookName,
                    number:this.number
                  }).then(() => {
                     alert("success");
                 }).catch((response) => {
                    alert(response);
                 });
         }

    },
